import { Grid } from "@mui/material";

export const ProjectsContainer = () => {
  return (
    <div className="bg-warning-subtle">
      <Grid
        container
        className="container text-center"
        direction={"column"}
        alignItems={"center"}
      >
        <Grid container justifyContent={"center"}>
          <Grid
            item
            xs={10}
            md={6}
            className="w-50 p-3 my-5 border bg-white shadow rounded"
          >
            <p className="fs-5 fw-bold m-0">1</p>
            <div className="d-flex justify-content-center">
              <p className="p-2"></p>
            </div>
            <p className="fs-5 fw-bold">, 51</p>
          </Grid>
        </Grid>
        <Grid container justifyContent={"center"}>
          <Grid
            item
            xs={10}
            md={6}
            className="w-50 p-3 mb-5 border bg-white shadow rounded"
          >
            <p className="fs-5 fw-bold m-0">2</p>
            <div className="d-flex justify-content-center">
              <p className="p-2"></p>
            </div>
            <p className="fs-5 fw-bold">, 21</p>
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
};
